
import { defineComponent } from 'vue'
import { COM20400312_REQ_BODY, COM20400312_RES, ScreenInfo } from '@/shared/type/COM20400312.type'
import { COM20400321_REQ_BODY, COM20400321_RES } from '@/shared/type/COM20400321.type'
import { COM20400331_REQ_BODY, COM20400331_RES } from '@/shared/type/COM20400331.type'
import { COMMON_KEY_VALUE } from '@/shared/constants/common.const'
import { modalController } from '@ionic/vue'
import BreadCrumb from '@/shared/components/BreadCrumb/BreadCrumb.vue';
import SelectOption from '@/shared/components/SelectOption/SelectOption.vue';
import ModifyCard from '@/shared/components/ModifyCard/ModifyCard.vue'
import { APM20200113_RES } from '@/shared/type/APM20200113.type';
import { screenMode } from '@/shared/type/Approval.type';
export default defineComponent({
    name: "com204003sc01Vue",
    components: { BreadCrumb, SelectOption, ModifyCard },
    data() {
        const modifyCard = {
            registerTellerName: '',
            registerTellerId: '',
            updatedTellerId: '',
            updatedTellerName: '',
            updatedDate: '',
            updatedTime: '',
            registerDate: '',
            registerTime: '',
            deleteTellerName: '',
            deleteTellerId: '',
            deleteDate: '',
            deleteTime: ''
        } as any
        return {
            modifyCard,
            COMMON_KEY_VALUE,
            isEnableEdit: false,
            isRegister: true,
            isInValidInput: false,
            screenInfo: {
                screenName: "",
                screenID: "",
                screenStatusCode: "",
                screenTypeCode: "",
                businessCategoryCode: "",
            } as ScreenInfo,
            businessCategoryItems: [] as any,
            approvalRes: {} as any,
            operationMode: 'normal' as screenMode
        }
    },
    async mounted() {
        if (this.approvalType === '') {
            const { type, screenID } = this.$route.query
            if (type === 'edit' && screenID !== '') {
                this.getScreenInfoDetail()
                this.isEnableEdit = false;
                this.isRegister = false;
            } else {
                this.isEnableEdit = true
                this.isRegister = true;
                this.screenInfo.screenStatusCode = this.screenStatusCode[0].key
            }
        } else {
            await this.$approval.getAuthorizationDetail().then((authDetail: APM20200113_RES) => {
                let authDetailParsed = JSON.parse(authDetail.body.requestContent)
                this.approvalRes = authDetail.body
                console.log(this.operationMode);
                this.operationMode = this.approvalType == 'my-request' ? 'read' : this.approvalType == 'my-approval' ? 'approval' : 'normal';
                this.screenInfo = {
                    screenName: authDetailParsed.screenName,
                    screenID: authDetailParsed.screenID,
                    screenStatusCode: authDetailParsed.screenStatusCode,
                    screenTypeCode: authDetailParsed.screenTypeCode,
                    businessCategoryCode: authDetailParsed.businessCategoryCode,
                } as any
            })
        }
    },
    computed: {
        BusinessCategoryCode(): string {
            return this.$store.state.CommonCodeModule.commonCode.BusinessCategoryCode;
        },
        screenStatusCode(): any {
            return this.$store.state.CommonCodeModule.commonCode.ScreenStatusCode
        },
        approvalType(): string {
            return this.$route.query.approvalType ? this.$route.query.approvalType.toString() : ''
        },
        renderPageTitle(): string {
            const { type } = this.$route.query
            if (type === 'edit') {
                return this.$t('com204003sc01.portal_screen_edit')
            } else {
                return this.$t('com204003sc01.portal_screen_register')
            }
        },
    },
    watch: {
        modifyCard() {
            if (this.screenInfo.screenStatusCode == '09') {
                this.modifyCard.deleteTellerName = this.modifyCard.updatedTellerName;
                this.modifyCard.deleteTellerId = this.modifyCard.updatedTellerId;
                this.modifyCard.deleteDate = this.modifyCard.updatedDate;
                this.modifyCard.deleteTime = this.modifyCard.updatedTime;
                this.modifyCard.updatedTime = '';
                this.modifyCard.updatedDate = '';
                this.modifyCard.updatedTellerId = '';
                this.modifyCard.updatedTellerName = '';
            }
        }
    },
    methods: {
        getCommonCodeList() {
            this.$store.state.CommonCodeModule.commonCode.ScreenStatusCode
        },
        async getScreenInfoDetail() {
            const reqBody: COM20400312_REQ_BODY = {
                screenID: this.$route.query.screenID as string,
            }
            const responseData = await this.$request.getRequest('COM20400312', reqBody, false) as COM20400312_RES
            if (responseData.header.result) {
                this.screenInfo = responseData.body;
                this.modifyCard = {
                    registerTellerName: responseData.body.firstRegisterTellerName,
                    registerTellerId: responseData.body.firstRegisterTellerID,
                    updatedTellerId: responseData.body.lastChangeTellerID,
                    updatedTellerName: responseData.body.lastChangeTellerName,
                    updatedDate: responseData.body.lastChangeDate,
                    updatedTime: responseData.body.lastChangeTime,
                    registerDate: responseData.body.firstRegisterDate,
                    registerTime: responseData.body.firstRegisterTime,
                } as any
            }
        },
        checkInValidInput(): boolean {
            const { screenID, screenName, screenStatusCode, screenTypeCode, businessCategoryCode } = this.screenInfo
            if (screenID === "" || screenName === "" || screenStatusCode === "" || screenTypeCode === "" || businessCategoryCode === "") {
                this.isInValidInput = true
                return true
            } else {
                this.isInValidInput = false
                return false
            }
        },

        onClickCancel() {
            let content = ''
            if (this.$route.query.type === 'register') {
                content = this.$t('message.move_to_list_without_saving_changes')
            } else {
                content = this.$t('message.cancel_without_saving_changes')
            }
            this.$dialog.onOpen({
                dialogMode: "Question",
                content,
                callback: () => this.callbackCancel()
            })
        },
        callbackCancel() {
            if (this.$route.query.type === 'register') {
                this.$router.push({ path: '/COM204003SC00' })
                modalController.dismiss()
            } else {
                this.isEnableEdit = false
                modalController.dismiss()
                this.getScreenInfoDetail()
            }
        },
        onClickList() {
            this.$router.push({ path: '/COM204003SC00' })
        },
        onClickSave() {
            if (this.checkInValidInput()) {
                this.isInValidInput = true;
            } else {
                this.$dialog.onOpen({
                    dialogMode: "Question",
                    content: this.$t('message.would_you_like_to_save'),
                    callback: () => {
                        modalController.dismiss()
                        const { type } = this.$route.query
                        if (type === 'register') {
                            this.onRegisterScreen()
                        } else {
                            this.onUpdateScreen()
                        }
                    }
                })
            }
        },
        async onRegisterScreen() {
            const reqBody: COM20400321_REQ_BODY = {
                businessCategoryCode: this.screenInfo.businessCategoryCode,
                screenTypeCode: this.screenInfo.screenTypeCode,
                screenName: this.screenInfo.screenName,
                screenID: this.screenInfo.screenID,
                screenStatusCode: this.screenInfo.screenStatusCode
            }
            const responseData = await this.$request.getRequest('COM20400321', reqBody, false) as COM20400321_RES
            if (responseData.header.result) {
                this.$router.push({ path: '/COM204003SC00' })
                this.$dialog.onOpen({
                    dialogMode: "Success",
                    content: this.$t('com204003sc01.register_screen'),
                    callback: () => modalController.dismiss()
                })
            }
            // this.$router.push({ path: '/COM204003SC00' })
        },
        async onUpdateScreen() {
            const reqBody: COM20400331_REQ_BODY = {
                businessCategoryCode: this.screenInfo.businessCategoryCode,
                screenTypeCode: this.screenInfo.screenTypeCode,
                screenName: this.screenInfo.screenName,
                screenID: this.screenInfo.screenID,
                screenStatusCode: this.screenInfo.screenStatusCode
            }
            const responseData = await this.$request.getRequest('COM20400331', reqBody, false) as COM20400331_RES
            if (responseData.header.result) {
                this.isEnableEdit = false;
                this.$dialog.onOpen({
                    dialogMode: "Success",
                    content: this.$t('com204003sc01.update_screen'),
                    callback: () => modalController.dismiss()
                })
                this.getScreenInfoDetail();
            }
        },
        onClickEnableEdit() {
            this.isEnableEdit = true;
            this.isRegister = false;
        },
    }
})
